import Table from "react-bootstrap/Table";
import "bootstrap/dist/css/bootstrap.min.css";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

const ProdTable = ({ uretim, page, sevk, talep }) => {
  const now = new Date();
  const day = now.getDate();
  const month = now.getMonth() + 1;
  const year = now.getFullYear();
  const formattedExcelName = `${day}-${month}-${year}-talep-raporu`;

  return (
    <>
      <Table id='table-to-xls' striped bordered hover>
        <thead>
          <tr>
            <th>Stok Kodu</th>
            <th>rn</th>
            <th>Talep (tepsi)</th>
            <th>retim (Tepsi)</th>
            <th>Talep (KG)</th>
            <th>retim (KG)</th>
            <th>Sevk (KG)</th>
          </tr>
        </thead>
        <tbody>
          {uretim[page] &&
            uretim[page].map((urun, index) => {
              return (
                <tr key={index}>
                  <td>{urun.stokKodu}</td>
                  <td>{urun.name}</td>
                  <td>
                    {talep[0] &&
                    talep[0][page] &&
                    talep[0][page][
                      talep[0][page].findIndex(
                        (talepUrun) => talepUrun.stokKodu === urun.stokKodu
                      )
                    ] &&
                    talep[0][page][
                      talep[0][page].findIndex(
                        (talepUrun) => talepUrun.stokKodu === urun.stokKodu
                      )
                    ].talepMiktari > 0
                      ? talep[0][page][
                          talep[0][page].findIndex(
                            (talepUrun) => talepUrun.stokKodu === urun.stokKodu
                          )
                        ].talepMiktari
                      : 0}
                  </td>
                  <td>{urun.uretimMiktari}</td>
                  <td>
                    {talep[0] &&
                    talep[0][page] &&
                    talep[0][page][
                      talep[0][page].findIndex(
                        (talepUrun) => talepUrun.stokKodu === urun.stokKodu
                      )
                    ] &&
                    parseFloat(
                      talep[0][page][
                        talep[0][page].findIndex(
                          (talepUrun) => talepUrun.stokKodu === urun.stokKodu
                        )
                      ].receteyeGoreKg
                    )
                      ? parseFloat(
                          talep[0][page][
                            talep[0][page].findIndex(
                              (talepUrun) =>
                                talepUrun.stokKodu === urun.stokKodu
                            )
                          ].receteyeGoreKg
                        )
                          .toFixed(2)
                          .replace(".", ",")
                      : 0}
                  </td>

                  <td>
                    {parseFloat(urun.receteyeGoreKg)
                      .toFixed(2)
                      .replace(".", ",")}
                  </td>

                  <td>
                    {sevk[page] &&
                    sevk[page][
                      sevk[page].findIndex(
                        (sevkurun) => sevkurun.stokKodu === urun.stokKodu
                      )
                    ] &&
                    parseFloat(
                      sevk[page][
                        sevk[page].findIndex(
                          (sevkurun) => sevkurun.stokKodu === urun.stokKodu
                        )
                      ].sevkMiktari
                    )
                      ? parseFloat(
                          sevk[page][
                            sevk[page].findIndex(
                              (sevkurun) => sevkurun.stokKodu === urun.stokKodu
                            )
                          ].sevkMiktari
                        )
                          .toFixed(2)
                          .replace(".", ",")
                      : 0}
                  </td>
                </tr>
              );
            })}
        </tbody>
      </Table>
      <ReactHTMLTableToExcel
        id='test-table-xls-button'
        className='download-table-xls-button'
        table='table-to-xls'
        filename={formattedExcelName}
        sheet='Rapor'
        buttonText='Raporu indir'
      />
    </>
  );
};

export default ProdTable;
